import { drizzleConnect } from "drizzle-react";
import React, { Children, Component } from "react";

// cost ComponentWithMemo = (update)
export default (interval) => {
  let inThrottle = false
  return (Component, mapStateToProps) => {
    const checkThrottle = () => {
      if (!inThrottle) {
        inThrottle = true
        setTimeout(() => inThrottle = false, interval)
        return true
      }
      return false
    }
    const MemoComponent = React.memo(props => <Component{...props} />, (prevProps, nextProps) => checkThrottle())

    return drizzleConnect(
      (props) => <MemoComponent {...props} />,
      mapStateToProps
    )
  }
}

