import React, { createContext, useContext, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

export const PreloadImageContext = createContext<any>('');

interface ProviderProps {
  children: any,//React.Component
}

export const PreloadImageProvider: React.FunctionComponent<ProviderProps> = ({ children }) => {
  const isLoaded = useRef(window.initialImageDetailsLoaded);

  useEffect(
    () => {
      isLoaded.current = window.initialImageDetailsLoaded
    },
    [window.initialImageDetailsLoaded]
  );
  return <PreloadImageContext.Provider value={{ isLoaded: isLoaded.current, anotherValue: 'test' }}>
    {children}
  </PreloadImageContext.Provider>
};

PreloadImageProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

export const usePreloadImage = () => useContext(PreloadImageContext);

export const connectPreloadImage = (WrappedComponent: any) => {
  return (props: any) => {
    const preloadImage = usePreloadImage()
    const newProps = { ...props, preloadImage }
    return <WrappedComponent {...newProps} />
  }
}
