const OfflineContainer = (props) => {
  if (props.drizzleStatus.initialized) {
    return Children.only(props.children);
  }

  if (!!props.alternateLoader) {
    return <props.alternateLoader />
  }

  if (props.web3.status === "") {
    return (
      <div className="section">
        This application is offline. Please ensure you have an Ethereum/web3 enabled browser.
        </div>
    )
  }


  return (
    <main className="container loading-screen">
      <div className="pure-g">
        <div className="pure-u-1-1">
          <h1></h1>
          <p>Loading dapp...</p>
        </div>
      </div>
    </main>
  );
}

/*
 * Export connected component.
 */

const mapStateToProps = (state) => {
  return {
    accounts: state.accounts,
    drizzleStatus: state.drizzleStatus,
    web3: state.web3,
  };
};

export default throttledDrizzelConnect(1000)(OfflineContainer, mapStateToProps);
