const alternateBasicImage = (index: number) => () =>
  <div className='image-container'>
    {window.initialImageDetailsLoaded &&
      <a href={`${window.initialImageDetails.urls[index]}?ref=alwaysforsale`}>
        <img
          src={window.rootCdnUrl + window.initialImageDetails.images[index]}
          style={{ width: '100%' }}
        />
      </a>
    }
  </div>

const throttleCreator = (limit: number) => {
  let inThrottle = false
  return function () {
    if (!inThrottle) {
      inThrottle = true
      setTimeout(() => inThrottle = false, limit)
      return true
    }
    return false
  }
}
const throttle = throttleCreator(1000)
class Dapp extends Component<any, any> {
  contracts: any;
  utils: any;
  inputs: any;
  state: any;

  static contextTypes = {
    drizzle: PropTypes.object
  };

  context: any;

  constructor(props: any, context: any) {
    super(props);
    // const tokenId = 0;
    // console.log(context.drizzle.contracts.VitalikSteward.methods.hashes.);
    //console.log(context.drizzle.contracts['VitalikSteward']['hashes']['0x0']);
    this.contracts = context.drizzle.contracts;
    this.utils = context.drizzle.web3.utils;
    this.context = context;

    this.state = {
      tokenOwner: '',
      hasheKey: context.drizzle.contracts.VitalikSteward.methods.hashes.cacheCall(props.tokenId),
      urlKey: context.drizzle.contracts.VitalikSteward.methods.urls.cacheCall(props.tokenId),
      hash: null,
      base64Image: '',
      websiteUrl: '',
      totalMonthRevenue: '',
      hasStartedLoadingIpfsImage: false,
    };
  }

  getBase64Image = async (hash: any) => {
    // console.log('getting bas64 hash')

    this.setState({
      ...this.state,
      hasStartedLoadingIpfsImage: true
    })

    // window.ipfsNode.cat(hash, async (err: any, data: any) => {
    //   if (err) return console.error(err);

    const data = await cat(hash)
    // convert Buffer back to string
    this.setState({
      ...this.state,
      base64Image: data.toString()
    });
    // })
  };

  calcTotalMonthRevenue = (ad1: number, ad2: number, ad3: number) => {
    this.setState({ totalMonthRevenue: (ad1 + ad2 + ad3) })
  }

  async componentWillReceiveProps(nextProps: any) {
    // console.log('here')

    if (this.state.urlKey in this.props.contracts['VitalikSteward']['urls'] &&
      this.state.urlKey in nextProps.contracts['VitalikSteward']['urls']
    ) {
      const urlNext =
        nextProps.contracts['VitalikSteward']['urls'][
          this.state.urlKey
        ].value;

      if (this.state.websiteUrl !== urlNext) {
        this.setState({ ...this.state, websiteUrl: urlNext })
      }
    }

    if (
      this.state.hasheKey in
      this.props.contracts['VitalikSteward']['hashes'] &&
      this.state.hasheKey in
      nextProps.contracts['VitalikSteward']['hashes']
    ) {
      const hashNext =
        nextProps.contracts['VitalikSteward']['hashes'][
          this.state.hasheKey
        ].value;

      // console.log('has next hash, checking if it is different:', hashNext)
      if (this.state.hash !== hashNext) {
        // console.log(
        //   'this.state.hash !== hashNext',
        //   this.state.hash !== hashNext,
        //   'hashNext !== window.initialImageDetails.images[nextProps.tokenId]',
        //   hashNext !== window.initialImageDetails.images[nextProps.tokenId],
        //   'this.state.hasStartedLoadingIpfsImage',
        //   this.state.hasStartedLoadingIpfsImage,
        //   '!!window.ipfsNode',
        //   !!window.ipfsNode,
        //   'this.state.hasStartedLoadingIpfsImage',
        //   this.state.hasStartedLoadingIpfsImage,
        // )
        // console.log('the next hash is::', hashNext)
        // console.log('the next hash is::', hashNext)
        // console.log('the next hash is::', hashNext)
        // console.log('the next hash is::', hashNext)
        if (
          hashNext !== window.initialImageDetails.images[nextProps.tokenId]
          || !this.state.hasStartedLoadingIpfsImage
        ) {
          this.getBase64Image(hashNext)
        } else {
          console.log('the ipfs hash won\'t load this time')
        }
        // this.setState({ ...this.state, hash: 'QmSKefBekHMWMPWnYGPQBziGcJD3QtCuw977Dv8VXAJLtb' })
        this.setState({ ...this.state, hash: hashNext })
      } else if (!this.state.hasStartedLoadingIpfsImage) {
        this.getBase64Image(hashNext)
      }
    }
    // });
    // this.setState({ ...this.state, hashes: newHashes });
    const { tokenId } = nextProps;
    const tokenOwnerKey = this.context.drizzle.contracts.ERC721Full.methods.ownerOf.cacheCall(
      tokenId
    );
    const tokenOwnerObj =
      nextProps.contracts['ERC721Full']['ownerOf'][tokenOwnerKey];

    if (
      !!tokenOwnerObj &&
      !!tokenOwnerObj.value &&
      this.state.tokenOwner !== tokenOwnerObj.value
    ) {
      this.setState({
        ...this.state,
        tokenOwner: tokenOwnerObj.value
      });
    }
  }

  // shouldComponentUpdate(nextProps: any, nextState: any) {
  //   // console.log('should update?')
  //   // return false
  //   return throttle()
  // }

  render() {
    const { tokenOwner } = this.state;
    const { accounts, tokenId } = this.props;
    const showDapp = web3ProvideSwitcher.providerInjected;
    const isTokenOwner = tokenOwner === accounts[0];
    // const displayPreloadedImages = window.initialImageDetailsLoaded
    const displayImage = !!this.state.base64Image //|| displayPreloadedImages
    const imageSrc =
      !!this.state.base64Image ?
        this.state.base64Image
        : ''
    // : window.initialImageDetailsLoaded ?
    //   window.rootCdnUrl + tokenId : ''
    // const altImgUrl = window.initialImageDetailsLoaded ?
    //   window.rootCdnUrl + window.initialImageDetails.images[tokenId] : ''
    const adLink = !!this.state.websiteUrl ? this.state.websiteUrl : /*displayPreloadedImages ? window.initialImageDetails.urls[tokenId] :*/ ''

    // console.log('the image url:', imageUrl)
    return (
      <Fragment>
        <OfflineContainer
          alternateLoader={alternateBasicImage(tokenId)}>
          <div className='image-container'>
            <a href={`${adLink}?ref=alwaysforsale`}>
              {displayImage ? ( //TODO undefined
                <img
                  src={imageSrc}
                  style={{ width: '100%' }}
                />
              )
                :
                <div
                  style={{ width: '100%' }}
                >
                  {/* <a href={`${imageDataLoaded ? window.initialImageDetails.urls[i] : '_'}?ref=alwaysforsale`}>
                  <img
                  src={window.rootCdnUrl + i}
                  style={{ width: '100%' }}
                  ></img>
                </a> */}
                  <h2>Loading...</h2>
                  <p style={{ backgroundColor: 'white' }}>This is slower than usual. We have temporarily disabled since some of our users were having problems with it.</p>
                </div>
              }
            </a>
            {this.props.displayPurchase && (
              <div className='interaction-button-container'>
                <div className='interaction-buttons'>
                  {isTokenOwner ? (
                    <div>
                      <Fragment>
                        <UpdateModal />
                        {/* <TokenOverview /> */}
                      </Fragment>
                    </div>
                  ) : (
                      <div>
                        {showDapp ? (
                          <BuyModal />
                        ) : (
                            <h3
                              style={{
                                margin: 0,
                                color: '#6bad3e',
                                padding: '0.8rem 1.2rem',
                                display: 'inline-block'
                              }}
                            >
                              Install <a href='https://metamask.io'>Metamask</a> to
                              buy this ad space.
                          {/* TODO: test if this is moblie and recommend a web3 app for android/iphone (eg trust-wallet)*/}
                            </h3>
                          )}
                        <TokenOverview />
                      </div>
                    )}
                </div>
              </div>
            )}
          </div>
        </OfflineContainer>
      </Fragment>
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    contracts: state.contracts,
    accounts: state.accounts
  };
};

// const throttle = throttleCreator(1000);
// const TransparentComponent = React.memo((props) => <Dapp {...props}></Dapp>, throttle);
// const AnotherWrapper = (props: any) => <TransparentComponent {...props}></TransparentComponent>
const DappConnected = connectTokenId(throttledDrizzelConnect(1000)(Dapp, mapStateToProps));

class DappWrapper extends Component<any, any> {
  constructor(props: any, context: any) {
    super(props);
  }

  render() {
    return (
      // <UsdPriceProvider>
      <OfflineContainer alternateLoader={alternateBasicImage(this.props.tokenId)}>
        <DappConnected displayPurchase={this.props.displayPurchase} tokenId={this.props.tokenId} />
        {/* <DappConnected throttle={throttleCreator(1000)} displayPurchase={this.props.displayPurchase} tokenId={this.props.tokenId} /> */}
      </OfflineContainer>
      // </UsdPriceProvider>
    );
  }
}

export default DappWrapper;
